import { commodityTypeMore } from "@/components/productList";
export default {
  data: function data() {
    return {
      tipsNum: 1,
      tips: false,
      autoplay: true,
      activeIndex: 0,
      vendorsList: [],
      aliexpressVendorsStatus: !localStorage.getItem("c_aliexpressVendorsStatus"),
      pickerOptions0: {
        disabledDate: function disabledDate(time) {
          return time.getTime() > Date.now() - 8.64e6;
        }
      },
      scrollContainer: this.$root.$children[0].$refs.mainScroll,
      card1Loading: false,
      card2Loading: false,
      card3Loading: false,
      vendorItems: [],
      items: [],
      totalCount: 0,
      items2: [],
      iList: [],
      invoiceInfo: "",
      shareTitle: "",
      shareDesc: "",
      sendInvoiceOpen: false,
      activeShareName: "shareWeixin",
      showDownloadInvoice: false,
      remarkLoading: false,
      noneData: 0,
      DialogEntrustStore: {
        isShow: false,
        loading: false,
        vendorName: "---",
        vendorId: "",
        shopName: "---",
        id: ""
      },
      DialogDispatchSetting: {
        isShow: false,
        loading: false,
        vendorName: "---",
        vendorId: "",
        storeName: "---",
        allProducts: 0,
        productIds: [],
        getProductsloading: false,
        products: [],
        period: "1"
      },
      DialogDispatchPreview: {
        isShow: false,
        loading: false,
        orderCnt: 0,
        vendorCnt: 0
      },
      statisticDate: [],
      statisticDefaultDate: [],
      statisticDateRange: "3",
      statisticDateFrom: "",
      statisticDateTo: "",
      dateArr: {
        1: "Today",
        2: "Last 7 days",
        3: "Last 30 days",
        4: "Last 90 days",
        5: "Last 12 months",
        6: "Custom"
      },
      userInfo: localStorage.getItem("c_ourMallUserInfo") ? JSON.parse(localStorage.getItem("c_ourMallUserInfo")) : "",
      tableData: [],
      moreReference: {
        isShow: false,
        pageSizes: [10, 20, 50, 100],
        page: 1,
        rowsPerPage: localStorage.getItem("c_moreReferenceRowsPerPage") ? Number(localStorage.getItem("c_moreReferenceRowsPerPage")) : 10,
        items: [],
        total: 0,
        totalPage: 0,
        loading: false,
        tableData: []
      },
      dashBoardData: {},
      marketLoading: true,
      categorysList: []
    };
  },
  created: function created() {
    this.tipsNum = localStorage.getItem("tips") || 1;

    if (this.tipsNum == 1) {
      this.tips = true;
      localStorage.setItem("tips", 2);
    } else {
      this.tips = false;
    }
  },
  mounted: function mounted() {
    var _this = this;

    if (localStorage.getItem("c_ourMallFirstLogin") && localStorage.getItem("c_ourMallFirstLogin") == 2) {
      //
      this.noneData = 0;
      this.getOrderItems();
    } else {
      //,dashboard
      this.noneData = 1;
    } //guidance
    //			let userInfo = localStorage.getItem('c_ourMallUserInfo') ? JSON.parse(localStorage.getItem('c_ourMallUserInfo')) : "";
    //			if(userInfo && userInfo.vendorCnt == 0 && !sessionStorage.getItem('c_haveShownGuide')){
    //				this.$router.push({name:"guidance"});
    //				return;
    //			}
    //			this.getDashboardVendors();
    //			this.getLastInvoice();


    this.$apiCall("api.Invoice.findStores", {}, function (r) {
      if (r.ErrorCode == 9999) {
        var stores = r.Data.Results; //					if(r.Data.Results.length > 0){
        //						this.DialogEntrustStore.id = r.Data.Results[0].id;
        //					}

        _this.$apiCall("api.Relationship.findByCustomer", {}, function (r) {
          if (r.ErrorCode == 9999) {
            if (r.Data.Results.length > 0) {
              var item = r.Data.Results.find(function (item) {
                return item.vendorId;
              });

              if (_this.$store.state.userInfo.isInvite == 1 && !Number(_this.$store.state.userInfo.ruleCnt) && stores.every(function (store) {
                return !store.vendorName;
              })) {
                stores.forEach(function (store, index) {
                  if (store.subShopUrl == localStorage.getItem("c_inviteShopUrl") && store.authStatus == 1 && _this.$store.state.userInfo.vendorCnt == "1") {
                    //
                    _this.DialogEntrustStore.shopName = stores[index].subShopUrl;
                    _this.DialogEntrustStore.id = stores[index].id;
                    if (item.vendorId) _this.DialogEntrustStore.vendorId = item.vendorId;
                    _this.DialogEntrustStore.vendorName = item.vendorName;
                    _this.DialogEntrustStore.isShow = true;
                    localStorage.removeItem("c_inviteShopUrl");
                  }
                });
              }
            }
          }
        });
      }
    });
    this.getDashboardSummary();
    this.myEcharts();
    this.getVnedors();
    this.getVendorShop();
  },
  components: {},
  computed: {
    ActiveCategoryName: function ActiveCategoryName() {
      return this.categorysList.length && this.categorysList[this.activeIndex].name;
    }
  },
  methods: {
    check2: function check2(e) {
      if (e) {
        //  e==true
        this.data.warning = false;
      } else {
        this.data.warning = true; //  e==false
      }
    },
    getVendorShopProducts: function getVendorShopProducts() {
      var _this2 = this;

      //getVendorShop 
      var num = this.categorysList.length;
      this.categorysList.forEach(function (category) {
        _this2.$apiCall("api.VendorShop.findProducts", {
          page: 1,
          rowsPerPage: 12,
          categoryId: category.id
        }, function (r) {
          num--;
          if (!num) _this2.marketLoading = false;

          if (r.ErrorCode == "9999") {
            category.items = r.Data.Results;
          } else {
            _this2.$elementMessage(r.Message, "error");
          }
        });
      });
    },
    getVendorShop: function getVendorShop() {
      var _this3 = this;

      this.categorysList = [];
      var categorys = commodityTypeMore;

      function randomNum(minNum, maxNum) {
        switch (arguments.length) {
          case 1:
            return parseInt(Math.random() * minNum + 1, 10);
            break;

          case 2:
            return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
            break;

          default:
            return 0;
            break;
        }
      } //5


      var getCNum = 5;

      var getCagegorys = function getCagegorys() {
        if (!getCNum) {
          _this3.getVendorShopProducts(); //


          return;
        }

        var num = randomNum(0, categorys.length);

        if (categorys[num]) {
          _this3.categorysList.push(categorys[num]);

          getCNum--;
        }

        categorys.splice(num, 1);
        setTimeout(function () {
          getCagegorys();
        }, 50);
      };

      getCagegorys();
    },
    deleteTramsform: function deleteTramsform(event) {// console.log(event)
      // console.log(this.$refs.pricingCarousel)
      // event.path[5].style = ''
    },
    productTag: function productTag(id) {
      this.$router.push({
        path: "/aliExpress-dropShipping-vendors",
        query: {
          typeid: id
        }
      });
    },
    addVendors: function addVendors(id, vendorId) {
      var _this4 = this;

      this.$showLoading();
      this.$apiCall("api.VendorShop.addToMyVendor", {
        id: id
      }, function (r) {
        if (r.ErrorCode == "9999") {
          // this.items = r.Data.Results;
          _this4.vendorsList.forEach(function (item) {
            if (item.vendorId === vendorId) {
              _this4.$set(item, "isAdded", 1);
            }
          });

          _this4.$elementMessage("Add vendors success", "success");
        } else {
          _this4.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this4.$userFailure();
          }
        }

        _this4.$hideLoading();
      });
    },
    closeAliexporessVendors: function closeAliexporessVendors() {
      this.aliexpressVendorsStatus = false;
      localStorage.setItem("c_aliexpressVendorsStatus", "1");
    },
    setActiveItem: function setActiveItem(i) {
      this.autoplay = false;
      this.$refs.pricingCarousel.setActiveItem(i);
    },
    handleChangeCarousel: function handleChangeCarousel() {
      this.activeIndex = this.$refs.pricingCarousel.activeIndex;
    },
    getVnedors: function getVnedors() {
      var _this5 = this;

      this.$apiCall("api.VendorShop.findByCustomer", {
        page: 1,
        rowsPerPage: 5,
        rand: true
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this5.vendorsList = r.Data.Results;

          _this5.vendorsList.forEach(function (item) {
            var arr = [];
            item.products.slice(0, 4).forEach(function (items) {
              arr.push(items.imgUrl);
            });

            _this5.$set(item, "imgUrlGroup", arr);
          });
        } else {
          _this5.$elementMessage(r.Message, "error");

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this5.$userFailure();
          }
        }
      });
    },
    gotoPage: function gotoPage(type) {
      switch (type) {
        case 1:
          this.$router.push({
            name: "WaitingForAllocated"
          });
          break;

        case 2:
          this.$router.push({
            name: "OrderProcessing",
            query: {
              status: 4
            }
          });
          break;

        case 3:
          this.$router.push({
            path: "/stores",
            query: {
              isAuth: "1"
            }
          });
          break;

        case 4:
          this.$router.push({
            name: "vendors"
          });
          break;

        case 5:
          this.$router.push({
            name: "importList"
          });
          break;

        case 6:
          this.$router.push({
            path: "/aliExpress-dropShipping-vendors"
          });
          break;

        default:
          this.$router.push({
            name: "products"
          });
          break;
      }
    },
    getTableData: function getTableData() {
      var _this6 = this;

      this.moreReference.loading = true;
      this.$apiCall("api.Invoice.dashboardStatistic", {
        page: this.moreReference.page,
        rowsPerPage: this.moreReference.rowsPerPage
      }, function (r) {
        _this6.moreReference.loading = false;

        if (r.ErrorCode == 9999) {
          _this6.moreReference.tableData = Object.values(r.Data.Results);

          if (_this6.moreReference.tableData.length) {
            _this6.moreReference.tableData.forEach(function (item) {
              _this6.$set(item, "dateCode1", Number(item.dateCode));

              _this6.$set(item, "date", item.dateCode.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3"));
            });

            _this6.moreReference.total = Number(r.Data.Pagination.totalCount);
            _this6.moreReference.totalPage = Number(r.Data.Pagination.totalPage);
            _this6.moreReference.tableData = _this6.moreReference.tableData.sort(_this6.sortData);
          }
        } else {
          _this6.$elementMessage(r.Message, "error");
        }
      });
    },
    sortData: function sortData(a, b) {
      return b.dateCode1 - a.dateCode1;
    },
    dateChange: function dateChange() {
      console.log(moment(this.statisticDate[0]).format("YYYYMMDD"));

      if (this.statisticDate && this.statisticDate.length > 1) {
        this.statisticDateFrom = moment(this.statisticDate[0]).format("YYYYMMDD");
        this.statisticDateTo = moment(this.statisticDate[1]).format("YYYYMMDD");
        this.$Burying({
          object: "4001",
          objectId: "".concat(this.statisticDateFrom, " - ").concat(this.statisticDateTo)
        });
        this.myEcharts(1);
        this.statisticDateRange = "";
      }
    },
    dateRangeChange: function dateRangeChange() {
      var type = this.statisticDateRange;

      switch (type) {
        case "1":
          this.statisticDateFrom = moment().format("YYYYMMDD");
          this.statisticDateTo = moment().format("YYYYMMDD");
          break;

        case "2":
          this.statisticDateFrom = moment().subtract(7, "d").format("YYYYMMDD");
          this.statisticDateTo = moment().format("YYYYMMDD");
          break;

        case "3":
          this.statisticDateFrom = moment().subtract(30, "d").format("YYYYMMDD");
          this.statisticDateTo = moment().format("YYYYMMDD");
          break;

        case "4":
          this.statisticDateFrom = moment().subtract(90, "d").format("YYYYMMDD");
          this.statisticDateTo = moment().format("YYYYMMDD");
          break;

        case "5":
          this.statisticDateFrom = moment().subtract(12, "M").format("YYYYMMDD");
          this.statisticDateTo = moment().format("YYYYMMDD");
          break;

        default:
          this.statisticDateFrom = "";
          this.statisticDateTo = "";
          break;
      }

      this.statisticDate = [this.statisticDateFrom, this.statisticDateTo];
      this.$Burying({
        object: "4001",
        objectId: "".concat(this.statisticDateFrom, " - ").concat(this.statisticDateTo)
      });
      this.myEcharts(1);
    },
    getDashboardSummary: function getDashboardSummary() {
      var _this7 = this;

      this.$showLoading();
      this.$apiCall("api.Invoice.dashboardSummary", {}, function (r) {
        _this7.$hideLoading();

        if (r.ErrorCode == 9999) {
          _this7.dashBoardData = r.Data.Results;
        } else {
          _this7.$elementMessage(r.Message, "error");
        }
      });
      this.$apiCall("api.Invoice.dashboardStatistic", {
        page: this.moreReference.page,
        rowsPerPage: this.moreReference.rowsPerPage
      }, function (r) {
        _this7.$hideLoading();

        if (r.ErrorCode == 9999) {
          _this7.tableData = Object.values(r.Data.Results);

          if (_this7.tableData.length) {
            _this7.tableData.forEach(function (item) {
              _this7.$set(item, "dateCode1", Number(item.dateCode));

              _this7.$set(item, "date", item.dateCode.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3"));
            });

            _this7.moreReference.total = Number(r.Data.Pagination.totalCount);
            _this7.moreReference.totalPage = Number(r.Data.Pagination.totalPage);
            _this7.tableData = _this7.tableData.sort(_this7.sortData);
          }
        } else {
          _this7.$elementMessage(r.Message, "error");
        }
      });
    },
    toPage: function toPage(val) {
      if (val != this.moreReference.page) this.moreReference.page = val;
      this.getTableData();
    },
    changePageSize: function changePageSize(val) {
      this.moreReference.rowsPerPage = val;
      this.moreReference.page = 1;
      localStorage.setItem("c_moreReferenceRowsPerPage", val);
      this.getTableData();
    },
    showMoreReference: function showMoreReference() {
      this.$Burying({
        object: "3012"
      });
