    myEcharts: function myEcharts(type) {
      var _this8 = this;

      var chart = document.getElementById("dashboardCharts");

      if (!type) {
        this.statisticDateFrom = moment().subtract(1, "M").format("YYYYMMDD");
        this.statisticDateTo = moment().format("YYYYMMDD");
        this.statisticDate = [this.statisticDateFrom, this.statisticDateTo];
      }

      this.$showLoading();
      var arr = [];
      this.$apiCall("api.Invoice.dashboardStatistic", {
        dateFrom: this.statisticDateFrom,
        dateTo: this.statisticDateTo
      }, function (r) {
        _this8.$hideLoading();

        if (r.ErrorCode == 9999) {
          arr = Object.values(r.Data.Results);

          if (arr.length) {
            arr.forEach(function (item) {
              _this8.$set(item, "date", item.dateCode.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3"));
            });
          }

          console.log(arr);
          var myChart = echarts.init(chart); // 

          var option = {
            title: {
              text: ""
            },
            tooltip: {
              trigger: "axis"
            },
            legend: {
              data: ["Total Revenue ".concat(_this8.$showSybmol()), "Total Cost"]
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            //						    toolbox: {
            //						        feature: {
            //						            saveAsImage: {}
            //						        }
            //						    },
            xAxis: {
              type: "category",
              boundaryGap: false,
              data: arr.map(function (item) {
                return item.date;
              })
            },
            yAxis: {
              type: "value"
            },
            series: [{
              name: "Total Revenue ".concat(_this8.$showSybmol()),
              type: "line",
              stack: "Total Revenue",
              data: arr.map(function (item) {
                return _this8.$exchangeRate(item.revenue);
              }),
              smooth: true
            }, {
              name: "Total Cost",
              type: "line",
              stack: "Total Cost",
              data: arr.map(function (item) {
                return _this8.$exchangeRate(item.cost);
              }),
              smooth: true
            }]
          };
          myChart.setOption(option); //

          setTimeout(function () {
            myChart.resize();
          }, 200);
          window.onresize = myChart.resize;
        } else {
          _this8.$elementMessage(r.Message, "error");
        }
      });
    },
