    saveDispatchSetting: function saveDispatchSetting(type) {
      var _this11 = this;

      if (this.DialogDispatchSetting.allProducts && !this.DialogDispatchSetting.productIds.length) {
        this.$elementMessage("Please select the products", "error");
        return;
      }

      if (!this.DialogDispatchSetting.vendorId) {
        this.$elementMessage("Please select the vendor", "error");
        return;
      }

      if (!this.DialogDispatchSetting.period) {
        this.$elementMessage("Please select the circle period", "error");
        return;
      }

      this.DialogDispatchSetting.loading = true;
      var params = {
        relationshipId: this.DialogDispatchSetting.vendorId,
        period: this.DialogDispatchSetting.period,
        visibilityType: 1,
        productIds: this.DialogDispatchSetting.productIds,
        status: 1
      };

      if (!this.DialogDispatchSetting.allProducts) {
        params.productIds = [];
      }

      this.$apiCall("api.DispatchOrderRule.addByCustomer", params, function (r) {
        if (r.ErrorCode == 9999) {
          _this11.$apiCall("api.DispatchOrderRule.testGenerate", {}, function (r) {
            _this11.DialogDispatchSetting.loading = false;
            _this11.DialogDispatchSetting.isShow = false;

            if (r.ErrorCode == 9999) {
              _this11.DialogDispatchPreview.vendorCnt = r.Data.Results.vendorCnt;
              _this11.DialogDispatchPreview.orderCnt = r.Data.Results.orderCnt;
              _this11.DialogDispatchPreview.isShow = true;
            } else {
              _this11.$elementMessage(r.Message, "error");
            }
          });
        } else {
          _this11.DialogDispatchSetting.loading = false;

          _this11.$elementMessage(r.Message, "error");
        }
      });
    },
    remoteMethodGetProducts: function remoteMethodGetProducts(query) {
      var _this12 = this;

