      this.DialogDispatchSetting.getProductsloading = true;
      this.$apiCall("api.Product.findByUser", {
        name: query
      }, function (r) {
        _this12.DialogDispatchSetting.getProductsloading = false;

        if (r.ErrorCode == 9999) {
          _this12.DialogDispatchSetting.products = [];
          r.Data.Results.forEach(function (e) {
            if (JSON.parse(e.imgsJson).length) e.img = JSON.parse(e.imgsJson)[0];

            _this12.DialogDispatchSetting.products.push(e);
          });
        }
      });
